import React from "react"
import { Container, Jumbotron, Row, Col } from "react-bootstrap"

const Hero = (props)=>{

    const h2style = {
        paddingLeft:"5px",
        marginTop:"-5px",
        marginBottom:"15px"
        
    }

    const h3style = {
        paddingLeft:"8px"
    }

    return(
        <Jumbotron className="bg-transparent jumbotron-fluid p-0">
            <Container fluid={true}>
                <Row className="justify-content-center py-5">
                    <Col md={8} sm={12}>
                        {props.title && <h1 className="display-1 font-weight-bolder">{props.title}</h1>}
                        {props.subTitle && <h2 className="display-4 font-weight-light" style={h2style}>{props.subTitle}</h2>}
                        {props.text && <h3 className="lead font-weight-light" style={h3style}>{props.text}</h3>}
                    </Col>
                </Row>
            </Container>
        </Jumbotron>
    )
}

export default Hero