import React, { Component } from "react"
//import monsterMarketplace from "../monsterMarketplace.png"
import exjobb from "../images/exjobb@1x.png"
import globen from "../images/jorden@1x.png"
import monstera from "../images/monstera@1x.png"
import stad from "../images/stad@1x.png"
import tyg from "../images/tyg@1x.png"
import { Container, Row } from "react-bootstrap"
import Card from "../components/Card"

class Carousel extends Component{
    constructor(props){
        super(props)
        this.state = {
            items:[
                {
                    id:0,
                    title:"Projektet",
                    subTitle:"A project I did as a part of my master thesis at Edvira. The project was to create an interactive visualization tool that shows statistics on people studying at university, made with the JavaScript library D3.",
                    imgSrc:exjobb, 
                    link:"https://erikrudhe.github.io/D3-chart-test2.0/?fbclid=IwAR1cm5Q0mtZV5iGrIhe8Qo2l51odn0fgE2it8eFU4bBKvSHiosOLw0cMKng",
                    selcted:false
                },
                {
                    id:1,
                    title:"Test1",
                    subTitle:"A simple 3D-visualization of statistics on a globe. Made in course: TNM061. The main programming languages was HTML, CSS and JavaScript togheter with the library THREEjs",
                    imgSrc:globen, 
                    link:"http://elonolsson.github.io/3D-Global-Visualization/Globelen/main.html?fbclid=IwAR1Fka46xwUchfjgMP6xM07YTsH15gCzmrd-I167hl98ZOdEp1M_wtlbQt0",
                    selcted:false
                },
                {
                    id:2,
                    title:"Test2",
                    subTitle:"A procedurally generated city created with the JavaScripts library WebGL in the course TNM084 ",
                    imgSrc:stad, 
                    link:"https://erikrudhe.github.io/TNM084---Procedurally-generated-city/?fbclid=IwAR3QgCOcXYco_x1GNfeFKYRiHAN9LF8AhEC3GCuuA6I_4wJgfx0LBXHm-bU",
                    selcted:false
                },
                {
                    id:3,
                    title:"Test2",
                    subTitle:"A buy and sell website for used flowers made in the course advanced web programming TDDD27. The website was made with React, Redux and NodeJS and is firebase as hosting service",
                    imgSrc:monstera, 
                    link:"https://monstera-marketplace.firebaseapp.com/signin?fbclid=IwAR01lOttGPPIVCGbuMXkMMhv_rh3NC88LhH2tGCep7rsNWF7Kc2UoQRwz7M",
                    selcted:false
                },
                {
                    id:4,
                    title:"Test2",
                    subTitle:"A fabric simulation for the course TNM085 at. The simulation was done in C ++ and OpenGL.",
                    imgSrc:tyg, 
                    link:"https://www.youtube.com/watch?v=mx1k_C8rC9Y&fbclid=IwAR2coSVD9agI2VuSeNs5jcfx0jvwmwAh9Kdz7XWdirGq8znM2cXiDm-iZtg",
                    selcted:false
                }
                
            ]

        }
    }

    handleCardClick = (id, card) => {

        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }


    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-center">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel